/* ************************
   02.1: Helper Classes
   ********************* */
.form-control {
  background-color: #fff;
  height: 60px;
  padding: 5px 20px;
  border-radius: 3px;
  width: 100%;
  border: 1px solid #E7ECF5;
}

.form-control:focus {
  box-shadow: none;
  border-color: #E7ECF5;
}

.form-control::placeholder {
  color: #4F5164 !important;
  opacity: 0.5;
}

.form-signin {
  display: flex;
  justify-content: flex-end;
  max-width: 430px;
}

.form-signin .form-group {
  margin-bottom: 0;
  margin-right: 15px;
}

.form-signin .form-control {
  background-color: rgba(242, 246, 255, 0.6);
  height: 40px;
  border: none;
}

.form-signin .form-control::placeholder {
  color: #000000 !important;
  opacity: .45;
  font-size: 14px;
}

.input-group {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  width: 100%;
}

.input-group .form-control {
  position: relative;
  flex: 1 1 auto;
  min-width: 0;
}

.input-group .form-control::placeholder {
  color: #ffffff !important;
  opacity: .60;
  font-size: 14px;
}

.input-group button {
  position: relative;
  z-index: 2;
  padding: 5px;
}

.user-profile {
  display: flex;
  align-items: center;
  margin-bottom: 17px;
  padding-bottom: 20px;
  border-bottom: 1px solid #E9F1FD;
}

.user-profile .user-avatar {
  margin-right: 16px;
  width: 44px;
  height: 44px;
  overflow: hidden;
}

.user-profile p {
  font-size: 14px;
}

.star-rating {
  height: 12px;
  line-height: 12px;
  overflow: hidden;
  position: relative;
  width: 68px;
  font-size: 12px;
  color: #ffe539;
}

.star-rating:before {
  content: "\f006\f006\f006\f006\f006";
  display: inline-block;
  font-family: FontAwesome;
  left: 0;
  letter-spacing: 2px;
  position: absolute;
  top: 0;
}

.star-rating span {
  width: 80%;
  float: left;
  left: 0;
  overflow: hidden;
  padding-top: 12px;
  position: absolute;
  top: 0;
  color: #ffe539;
}

.star-rating span:before {
  content: "\f005\f005\f005\f005\f005";
  display: inline-block;
  font-family: FontAwesome;
  left: 0;
  letter-spacing: 2px;
  position: absolute;
  top: 0;
}

.hexagon {
  -webkit-clip-path: polygon(50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
  clip-path: polygon(50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
}

.section-bg {
  background-color: #F4F7FF;
}

.section-bg2 {
  background-color: #EDF2F6;
}

.radius-50 {
  border-radius: 50px !important;
}

.py-40 {
  padding-top: 40px;
  padding-bottom: 40px;
}

.video-frame {
  overflow: hidden;
  line-height: 1;
}

.video-frame iframe {
  width: 100%;
  height: 320px;
  border: none;
}

.card {
  border: none;
}

/* Lists */
.list-inline li {
  display: inline-block;
}

.list-dot-inline {
  display: flex;
  align-items: center;
}

.list-dot-inline li:not(:last-child) {
  margin-right: 15px;
  padding-right: 15px;
  position: relative;
}

.list-dot-inline li:not(:last-child):after {
  width: 3px;
  height: 3px;
  background-color: #888DA8;
  right: -1.5px;
  top: 50%;
  transform: translateY(-50%);
  position: absolute;
  content: '';
}

.list-dot-inline li a {
  display: flex;
  align-items: center;
  color: #888DA8;
}

.list-dot-inline li a svg {
  margin-right: 5px;
}

.list-dot-inline li a:hover {
  color: #888DA8;
}

.list-dot-inline li a:after {
  display: none;
}

.list-dot-inline.style2 li {
  font-size: 14px;
  color: #888DA8;
}

.list-check {
  display: flex;
  flex-wrap: wrap;
}

.list-check li {
  flex-basis: 50%;
  padding-left: 30px;
  position: relative;
  line-height: 1.2;
  margin-bottom: 20px;
  color: #4F5164;
}

@media only screen and (max-width: 479px) {
  .list-check li {
    flex-basis: 100%;
  }
}

